.Main-div {
    display: flex;
    flex-direction: column;
    align-items: center;
}

h1 {
    font-family: monospace, sans-serif;
}

#searchbar {
    font-family: monospace;
    width: 400px;
    height: 30px;
    border: 1px solid rgb(185, 184, 184);
    border-radius: 5px;
}

#button {
    font-family: monospace;
    color: white;
    cursor: pointer;
    background-color: rgb(114, 114, 184);
    height: 35px;
    width: 70px;
    margin-left: 10px;
    border-radius: 5px;
    border: none;
}

#headshot {
    height: 200px;
    width: 200px;
    object-fit: cover;
}

.Search-item {
    border: 1px solid black;
}
